
import React, { useEffect, useCallback } from 'react'

export default function proxyHOC(WrappedComponent) {
    return function(props) {
        useEffect(() => {
            console.log('HOC log');
            return () => {
            };
        }, []);
        const onClickHandler = useCallback(() => {
            console.log('onclick, in HOC log: ', this);
        });
        return (
            <>
                <header>1</header>
                <WrappedComponent {...props} onClick={() => onClickHandler()}></WrappedComponent>
            </>
        )
    }
}
